.dropdown-wrapper{position:relative}.dropdown-wrapper .dropdown-input{padding-right:17px}.dropdown-wrapper .dropdown-input.withClearButton{padding-right:26px}.dropdown-wrapper.active .dropdown-input{border-color:var(--active)}.dropdown-wrapper.active>.dropdown-arrow{transform:none;top:10px}.dropdown-wrapper>.dropdown-clear{position:absolute;color:var(--text);right:15px;font-weight:500;font-size:1.2em;cursor:default}.dropdown-wrapper>.dropdown-arrow{width:0;height:0;border-style:solid;border-width:0 5px 8.6px 5px;border-color:transparent transparent var(--text) transparent;transform:rotate(180deg);position:absolute;top:12px;right:4px;pointer-events:none}.dropdown-wrapper>.dropdown-arrow.disabled{opacity:0.5}.dropdown-wrapper .options-container{border:1px solid var(--active);overflow-y:auto;position:absolute;width:100%;box-sizing:border-box;background:white;margin-top:-1px;z-index:1;background:var(--cellBg)}.dropdown-wrapper .options-container .pinned-value-separator{border-bottom:1px dashed var(--separator)}.dropdown-wrapper .dropdown-option{padding:0.1em 0.5em;font-weight:400;color:var(--text)}.dropdown-wrapper .dropdown-option:hover,.dropdown-wrapper .dropdown-option.selected,.dropdown-wrapper .dropdown-option.hovered{background:var(--active)}.dropdown-wrapper.unfilterable .dropdown-input{cursor:default}

.table-pagination{display:flex;align-items:center}.table-pagination>*{white-space:nowrap}.table-pagination .table-paging-buttons-container{margin:0 1rem}.table-pagination .table-page-nav-button{border:none;font-weight:600;padding:0.5rem;min-width:31px;font-size:0.8rem;background:none;color:var(--textAgainstNoBg);cursor:pointer}.table-pagination .table-page-nav-button:not(:disabled):hover{color:var(--links)}.table-pagination .table-page-nav-button.active{background:var(--headerCellBg);color:var(--headerCellText);font-weight:800;cursor:default}.table-pagination .table-page-nav-button:disabled:not(.active){opacity:0.6;cursor:default}.table-pagination .ellipsis{margin:0 0.5rem}.table-pagination .dropdown-wrapper{width:60px;display:inline-block;margin-left:0.5rem;color:inherit}.table-pagination .dropdown-wrapper input{padding:0.5rem;border:1px solid var(--separator)}.table-pagination .dropdown-wrapper .dropdown-option{font-size:0.86em}@media all and (max-width: 585px){.table-pagination{flex-wrap:wrap;justify-content:space-between;margin-top:-0.5rem}.table-pagination>*{margin-top:0.5rem !important}.table-pagination>label:first-child{margin-right:1rem}.table-pagination .table-paging-buttons-container{order:2;margin:0}}

.table-action-search{transition:all 0.3s;overflow:hidden;width:16px;display:flex;align-items:center;position:relative}.table-action-search.active{width:100%}.table-action-search.active .icon{z-index:2}.table-action-search.active .table-search-input{margin-left:-16px;width:100%}.table-action-search.active .table-search-clear{font-size:2rem;position:absolute;right:0;color:var(--headerText);cursor:default}.table-action-search.active .table-search-clear:hover{color:var(--text)}.table-action-search .table-search-input{border:none;border-bottom:3px solid var(--links);background:none;padding:0.2rem;padding-left:24px;padding-right:20px;font-size:inherit;color:inherit}.table-action-search .table-search-input:focus,.table-action-search .table-search-input:active{outline:none;border-bottom-color:var(--active)}

.icon{flex-shrink:0}.icon.clickable{cursor:pointer}

.table-column-filter{margin-top:0.5rem}.table-column-filter input{font-size:0.85rem}.table-column-filter input[type='number']{text-align:inherit}.table-column-filter .dropdown-wrapper.active.popped .dropdown-input{z-index:2}.table-column-filter .dropdown-wrapper.active.popped .options-container{position:fixed !important;top:50% !important;left:50% !important;transform:translate(-50%, -50%) !important;z-index:2 !important;width:auto;max-height:90vh;cursor:default;min-width:120px}.table-column-filter .dropdown-option{font-size:0.85rem;padding:0 5px}.table-column-filter .money{position:relative}.table-column-filter .money::before{content:attr(data-moneySymbol, "$");color:var(--headerText);position:absolute;font-size:0.8em;font-weight:300;left:6px;top:1px}.table-column-filter .money input{padding-left:17px}.table-column-filter .react-datepicker__input-container input{padding-right:19px}.table-column-filter .react-datepicker__input-container input.react-datepicker-ignore-onclickoutside{z-index:2;position:relative}.table-ranged-filter-wrapper{display:flex;flex-wrap:wrap;margin:0 -2px}.table-ranged-filter-wrapper>div{position:relative;margin:2px;flex:1 1;flex-basis:50px;max-width:100px}.table-ranged-filter-wrapper input{width:100%}.table-ranged-filter-wrapper.date-filter-wrapper>div{max-width:200px;flex-basis:200px}.table-color-filter-preview{width:15px;height:15px;border-radius:15px;display:inline-block;margin-right:5px;vertical-align:middle}.react-datepicker__close-icon::after{background:none !important;color:var(--headerText) !important;font-size:1em !important;line-height:unset !important;display:block !important;position:absolute;right:0;top:4px;cursor:default}.react-datepicker__close-icon::after:hover{color:var(--text)}.table-date-picker-popup{position:fixed !important;top:50% !important;left:50% !important;transform:translate(-50%, -50%) !important;z-index:2 !important}.table-date-picker-popup .react-datepicker__triangle{display:none !important}.table-popup-filter-b